import React, { useState, useEffect, useMemo, useCallback } from "react";
import { Chart, registerables } from "chart.js";
import { Line } from "react-chartjs-2";
import axios from "axios";
import {
  Container,
  Grid,
  Paper,
  Card,
  Typography,
  LinearProgress,
  Tabs,
  Tab,
  Box,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  List,
  ListItem,
  ListItemIcon,
  Chip,
  useTheme,
  Alert,
  Button
} from "@mui/material";
import {
  CheckCircle as GoodIcon,
  Warning as ModerateIcon,
  Error as UnhealthyIcon,
  LocalHospital as HospitalIcon,
  Refresh as RefreshIcon
} from "@mui/icons-material";
import { motion } from "framer-motion";
import { throttle } from "lodash";

Chart.register(...registerables);

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000/ws";
const MAX_DATA_POINTS = 1000; // Increased buffer for high-frequency data

const AnalyticsPage = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [wsError, setWsError] = useState(null);
  const [lastUpdate, setLastUpdate] = useState(null);
  const theme = useTheme();

  // Process and validate incoming data
  const processIncomingData = useCallback((rawData) => {
    const now = new Date();
    return rawData.map(item => ({
      ...item,
      timestamp: item.timestamp || now.toISOString(),
      pm25: parseFloat(item.pm25) || 0,
      temp: parseFloat(item.temp) || 0,
      humidity: parseFloat(item.humidity) || 0,
      wind: parseFloat(item.wind) || 0,
      // Add derived timestamp fields for easier grouping
      hour: new Date(item.timestamp || now).getHours(),
      day: new Date(item.timestamp || now).getDay(),
      date: new Date(item.timestamp || now).toLocaleDateString()
    }));
  }, []);

  // Throttled data update to prevent UI lag
  const throttledSetData = useMemo(() => 
    throttle((newData) => {
      setData(prev => [...prev, ...newData].slice(-MAX_DATA_POINTS));
      setLastUpdate(new Date());
    }, 1000), []);

  useEffect(() => {
    // Initial data load
    const fetchData = async () => {
      try {
        const response = await axios.get(API_URL);
        setData(processIncomingData(response.data).slice(-MAX_DATA_POINTS));
        setLastUpdate(new Date());
      } catch (error) {
        console.error("Error fetching initial data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();

    // WebSocket connection
    const socket = new WebSocket(WS_URL);

    socket.onopen = () => {
      console.log("WebSocket connected");
      setWsError(null);
    };

    socket.onmessage = (event) => {
      try {
        const newData = JSON.parse(event.data);
        if (newData?.pm25 !== undefined) {
          const processed = processIncomingData([newData]);
          throttledSetData(processed);
        }
      } catch (error) {
        console.error("Error processing WebSocket message:", error);
      }
    };

    socket.onerror = (error) => {
      console.error("WebSocket error:", error);
      setWsError("Disconnected from real-time updates. Trying to reconnect...");
    };

    socket.onclose = () => {
      console.log("WebSocket disconnected");
      setWsError("Disconnected from real-time updates. Refresh page to reconnect.");
    };

    return () => {
      socket.close();
      throttledSetData.cancel();
    };
  }, [processIncomingData, throttledSetData]);

  const currentData = data.length > 0 ? data[data.length - 1] : null;

  if (loading) {
    return (
      <Container maxWidth="xl" sx={{ py: 4, textAlign: "center" }}>
        <LinearProgress />
      </Container>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {wsError && (
        <Alert severity="warning" sx={{ mb: 3 }}>
          {wsError}
        </Alert>
      )}

      <Box display="flex" justifyContent="space-between" alignItems="center">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Typography
            variant="h3"
            fontWeight="bold"
            sx={{
              mb: 3,
              background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
              fontSize: { xs: "2rem", md: "2.5rem" }
            }}
          >
            Advanced Air Quality Analytics
          </Typography>
        </motion.div>
        <Button 
          variant="outlined" 
          startIcon={<RefreshIcon />}
          onClick={() => setLastUpdate(new Date())}
        >
          Last update: {lastUpdate ? lastUpdate.toLocaleTimeString() : 'Never'}
        </Button>
      </Box>

      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <TimeSeriesAnalysis data={data} lastUpdate={lastUpdate} />
        </Grid>

        <Grid item xs={12} md={4}>
          <HealthImpactAssessment pm25={currentData?.pm25 || 0} />
        </Grid>

        
        <Paper
            elevation={0}
            sx={{
              p: 4,
              borderRadius: 4,
              bgcolor: 'background.paper',
              boxShadow: theme.shadows[4],
              width: '100%',
              border: `1px solid ${theme.palette.divider}`
            }}
          >
            <Box display="flex" alignItems="center" mb={3}>            
          <ForecastComponent historicalData={data} lastUpdate={lastUpdate} />          
            </Box>            
          </Paper>
      </Grid>
    </Container>
  );
};

const TimeSeriesAnalysis = ({ data, lastUpdate }) => {
  const [timeRange, setTimeRange] = useState("24h");
  const [parameter, setParameter] = useState("pm25");
  const theme = useTheme();

  // Create time buckets based on the selected range
  const createTimeBuckets = useCallback((data, range) => {
    const now = new Date();
    const buckets = {};
    
    data.forEach(item => {
      const date = new Date(item.timestamp);
      const hoursDiff = (now - date) / (1000 * 60 * 60);
      
      // Filter by time range
      if (range === "24h" && hoursDiff > 24) return;
      if (range === "7d" && hoursDiff > 168) return;

      // Create bucket key based on time range
      let bucketKey;
      if (range === "24h") {
        // Group by 15-minute intervals for high-frequency data
        const minutes = Math.floor(date.getMinutes() / 15) * 15;
        bucketKey = `${String(date.getHours()).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
      } else if (range === "7d") {
        // Group by day + hour for 7d view
        bucketKey = `${date.toLocaleDateString([], { weekday: "short" })} ${String(date.getHours()).padStart(2, '0')}:00`;
      } else {
        // Group by date for all data
        bucketKey = date.toLocaleDateString([], { month: "short", day: "numeric" });
      }

      if (!buckets[bucketKey]) {
        buckets[bucketKey] = {
          sum: 0,
          count: 0,
          max: -Infinity,
          min: Infinity,
          timestamp: date
        };
      }
      
      const value = item[parameter];
      buckets[bucketKey].sum += value;
      buckets[bucketKey].count++;
      buckets[bucketKey].max = Math.max(buckets[bucketKey].max, value);
      buckets[bucketKey].min = Math.min(buckets[bucketKey].min, value);
    });

    // Convert to array and sort by timestamp
    return Object.entries(buckets)
      .map(([key, value]) => ({
        key,
        ...value,
        avg: value.sum / value.count
      }))
      .sort((a, b) => a.timestamp - b.timestamp);
  }, []);

  const chartData = useMemo(() => {
    if (!data.length) return { labels: [], datasets: [] };

    const timeBuckets = createTimeBuckets(data, timeRange);
    
    return {
      labels: timeBuckets.map(b => b.key),
      datasets: [
        {
          label: 'Average',
          data: timeBuckets.map(b => b.avg),
          borderColor: theme.palette.primary.main,
          backgroundColor: `${theme.palette.primary.main}20`,
          tension: 0.3,
          fill: true
        },
        {
          label: 'Max',
          data: timeBuckets.map(b => b.max),
          borderColor: theme.palette.error.main,
          borderWidth: 1,
          pointRadius: 0
        },
        {
          label: 'Min',
          data: timeBuckets.map(b => b.min),
          borderColor: theme.palette.success.main,
          borderWidth: 1,
          pointRadius: 0
        }
      ]
    };
  }, [data, timeRange, parameter, createTimeBuckets, theme, lastUpdate]);

  return (
    <Card sx={{ p: 3, height: "100%" }}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
        <Typography variant="h6">Time Series Analysis</Typography>
        <Box display="flex" gap={2}>
          <FormControl size="small" sx={{ minWidth: 120 }}>
            <InputLabel>Parameter</InputLabel>
            <Select
              value={parameter}
              onChange={e => setParameter(e.target.value)}
              label="Parameter"
            >
              <MenuItem value="pm25">PM2.5</MenuItem>
              <MenuItem value="temp">Temperature</MenuItem>
              <MenuItem value="humidity">Humidity</MenuItem>
              <MenuItem value="wind">Wind Speed</MenuItem>
            </Select>
          </FormControl>
          <Tabs
            value={timeRange}
            onChange={(e, newValue) => setTimeRange(newValue)}
            size="small"
          >
            <Tab label="24h" value="24h" />
            <Tab label="7d" value="7d" />
            <Tab label="All" value="all" />
          </Tabs>
        </Box>
      </Box>

      <Box sx={{ height: "400px" }}>
        <Line
          data={chartData}
          options={{
            responsive: true,
            maintainAspectRatio: false,
            animation: {
              duration: lastUpdate ? 500 : 0 // Smooth animation for updates
            },
            plugins: {
              tooltip: {
                callbacks: {
                  label: (context) => {
                    const label = context.dataset.label || '';
                    return `${label}: ${context.parsed.y.toFixed(2)} ${getUnit(parameter)}`;
                  }
                }
              }
            },
            scales: {
              x: {
                title: { display: true, text: "Time" },
                ticks: {
                  maxRotation: 45,
                  minRotation: 45
                }
              },
              y: {
                title: { display: true, text: getUnit(parameter) },
                min: 0
              }
            }
          }}
        />
      </Box>
    </Card>
  );
};

const ForecastComponent = ({ historicalData, lastUpdate }) => {
  const [forecastData, setForecastData] = useState(null);
  const theme = useTheme();

  useEffect(() => {
    if (historicalData?.length > 0) {
      // Use last 3 hours of data for forecasting
      const recentHours = 3;
      const recentData = historicalData
        .filter(item => {
          const hoursDiff = (new Date() - new Date(item.timestamp)) / (1000 * 60 * 60);
          return hoursDiff <= recentHours;
        })
        .map(d => d.pm25)
        .filter(val => !isNaN(val));
      
      if (recentData.length < 2) {
        setForecastData(null);
        return;
      }

      // Calculate exponential moving average
      const smoothingFactor = 0.3;
      let ema = recentData[0];
      const emaValues = recentData.map((val, i) => {
        ema = i === 0 ? val : (smoothingFactor * val) + ((1 - smoothingFactor) * ema);
        return ema;
      });

      // Calculate trend based on EMA
      const trend = emaValues[emaValues.length - 1] - emaValues[0];
      
      // Generate forecast
      const forecast = Array.from({ length: 24 }, (_, i) => ({
        hour: `${i}:00`,
        pm25: Math.max(5, emaValues[emaValues.length - 1] + (trend * (i / recentHours)))
      }));

      setForecastData(forecast);
    }
  }, [historicalData, lastUpdate]);

  if (!forecastData) return (
    <Card sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        24-Hour PM2.5 Forecast
      </Typography>
      <LinearProgress />
    </Card>
  );

  return (
    <Card sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        24-Hour PM2.5 Forecast (Updated: {lastUpdate?.toLocaleTimeString() || 'Never'})
      </Typography>
      <Box sx={{ height: "400px" }}>
        <Line
          data={{
            labels: forecastData.map(d => d.hour),
            datasets: [
              {
                label: 'Forecasted PM2.5',
                data: forecastData.map(d => d.pm25),
                borderColor: theme.palette.secondary.main,
                backgroundColor: `${theme.palette.secondary.main}20`,
                tension: 0.4,
                fill: true
              },
              {
                label: 'Safety Threshold',
                data: forecastData.map(() => 50),
                borderColor: theme.palette.error.main,
                borderDash: [5, 5],
                borderWidth: 1,
                pointRadius: 0
              }
            ]
          }}
          options={{
            responsive: true,
            maintainAspectRatio: false,
            animation: {
              duration: lastUpdate ? 500 : 0
            },
            plugins: {
              annotation: {
                annotations: {
                  thresholdLine: {
                    type: 'line',
                    yMin: 50,
                    yMax: 50,
                    borderColor: 'red',
                    borderWidth: 1,
                    borderDash: [6, 6]
                  }
                }
              }
            },
            scales: {
              y: {
                title: { display: true, text: 'PM2.5 (µg/m³)' },
                suggestedMin: Math.max(0, Math.min(...forecastData.map(d => d.pm25))) - 5,
                suggestedMax: Math.max(...forecastData.map(d => d.pm25)) + 5
              },
              x: {
                title: { display: true, text: 'Hour' }
              }
            }
          }}
        />
      </Box>
    </Card>
  );
};

const HealthImpactAssessment = ({ pm25 }) => {
  const getAqiCategory = (pm25) => {
    if (pm25 <= 50) return 'Good';
    if (pm25 <= 100) return 'Moderate';
    if (pm25 <= 150) return 'Unhealthy for Sensitive Groups';
    if (pm25 <= 200) return 'Unhealthy';
    if (pm25 <= 300) return 'Very Unhealthy';
    return 'Hazardous';
  };

  const getRecommendations = (category) => {
    switch(category) {
      case 'Good':
        return ['Ideal air quality', 'No restrictions on outdoor activities'];
      case 'Moderate':
        return [
          'Unusually sensitive people should consider reducing prolonged outdoor exertion',
          'Generally acceptable for most people'
        ];
      case 'Unhealthy for Sensitive Groups':
        return [
          'People with heart or lung disease, older adults, and children should reduce prolonged outdoor exertion',
          'Everyone else should limit prolonged outdoor exertion'
        ];
      case 'Unhealthy':
        return [
          'Everyone should reduce prolonged outdoor exertion',
          'Sensitive groups should avoid outdoor activities'
        ];
      default:
        return [
          'Avoid all outdoor activities',
          'Keep windows and doors closed',
          'Use air purifiers if available'
        ];
    }
  };

  const category = getAqiCategory(pm25);
  const recommendations = getRecommendations(category);

  const getCategoryColor = () => {
    switch(category) {
      case 'Good': return 'success';
      case 'Moderate': return 'warning';
      default: return 'error';
    }
  };

  const getCategoryIcon = () => {
    switch(category) {
      case 'Good': return <GoodIcon color="success" />;
      case 'Moderate': return <ModerateIcon color="warning" />;
      default: return <UnhealthyIcon color="error" />;
    }
  };

  return (
    <Card sx={{ p: 3, height: "100%" }}>
      <Box display="flex" alignItems="center" mb={2}>
        <Typography variant="h6" sx={{ mr: 2 }}>
          Health Impact Assessment
        </Typography>
        <Chip 
          label={`AQI: ${category}`} 
          color={getCategoryColor()} 
          icon={getCategoryIcon()}
          variant="outlined"
        />
      </Box>

      <Typography variant="subtitle1" gutterBottom>
        Current PM2.5: {pm25} µg/m³
      </Typography>

      <List>
        {recommendations.map((rec, index) => (
          <ListItem key={index} sx={{ py: 0.5 }}>
            <ListItemIcon sx={{ minWidth: 32 }}>
              <HospitalIcon color="action" fontSize="small" />
            </ListItemIcon>
            <Typography variant="body2">{rec}</Typography>
          </ListItem>
        ))}
      </List>

      <Box mt={2}>
        <Typography variant="caption" color="text.secondary">
          Based on EPA Air Quality Index standards
        </Typography>
      </Box>
    </Card>
  );
};


const getUnit = (param) => {
  switch(param) {
    case 'pm25': return 'µg/m³';
    case 'temp': return '°C';
    case 'humidity': return '%';
    case 'wind': return 'km/h';
    default: return '';
  }
};

export default AnalyticsPage;